* {
  margin: 0;
  padding: 0;
}

:focus {
  outline: none;
}

body {
  scroll-behavior: smooth;
}

img {
  line-height: 0;
  display: block;
}

a {
  text-decoration-line: none;
}

ul {
  list-style-type: none;
}

button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  display: block;
}

html {
  scroll-behavior: smooth;
}

.main {
  background-color: #282828;
  display: flex;
}

.wrap {
  justify-content: center;
  gap: 20px;
  width: 100%;
  display: flex;
}

.room {
  background-color: #444;
  background-image: url("https://i.postimg.cc/Pq4T9SWb/floar-1.png"), url("https://i.postimg.cc/rmDy4S4S/wall-1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  width: 983px;
  height: 653px;
  position: relative;
}

.room__img {
  pointer-events: all;
  user-select: none;
  width: max-content;
  position: absolute;
  transform: translate(-50%, -50%);
}

.menu {
  z-index: 2;
  border-radius: 8px;
  min-width: 150px;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.menu__list {
  background-color: #313131;
  flex-direction: column;
  display: flex;
}

.menu__item {
  width: 100%;
}

.menu__item:first-child .menu__btn {
  background-color: #282b31;
}

.menu__item:nth-child(2) .menu__btn, .menu__item:nth-child(3) .menu__btn {
  background-color: #312e28;
}

.menu__item:nth-child(4) .menu__btn {
  background-color: #312828;
}

.menu__item:first-child .menu__btn:hover {
  background-color: #2889de;
}

.menu__item:nth-child(2) .menu__btn:hover, .menu__item:nth-child(3) .menu__btn:hover {
  background-color: #f5c92c;
}

.menu__item:nth-child(4) .menu__btn:hover {
  background-color: #ee3131;
}

.menu__item:first-child .menu__text {
  color: #2889de;
}

.menu__item:nth-child(2) .menu__text, .menu__item:nth-child(3) .menu__text {
  color: #f5c92c;
}

.menu__item:nth-child(4) .menu__text {
  color: #ee3131;
}

.menu__btn:hover .menu__text {
  color: #fff;
}

.menu__btn {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 8px 12px;
  display: flex;
}

.menu__item .menu__btn:hover .menu__icon {
  fill: #fff;
}

.menu__item:first-child .menu__icon {
  fill: #2889de;
}

.menu__item:nth-child(2) .menu__icon, .menu__item:nth-child(3) .menu__icon {
  fill: #f5c92c;
}

.menu__item:nth-child(4) .menu__icon {
  fill: #ee3131;
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
}

.button {
  background-color: #7e7e7e;
  border-radius: 50%;
  height: min-content;
  line-height: 0;
}

.button__icon {
  fill: #fff;
}

.sidebar {
  background-color: #313131;
  width: 400px;
  min-width: 400px;
  height: 100vh;
  padding: 40px;
  display: block;
  overflow: auto;
}

.sidebar::-webkit-scrollbar {
  display: none;
}

.sidebar__list {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.sidebar__item {
  background-color: #282828;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 150px;
  display: flex;
}

.sidebar__img {
  object-fit: contain;
  width: 120px;
  height: 120px;
}
